import React, { useState, } from 'react';
import { Link } from 'react-router-dom';
import { format, differenceInDays } from 'date-fns';
import {
    Container,
    Typography,
    Table,
    TableHead,
    TableRow,
    TableCell,
    TableBody,
    Select,
    MenuItem,
    IconButton,
    Switch,
    FormControlLabel,
    Button
} from '@mui/material';
import DeleteIcon from '@mui/icons-material/Delete';
import { v4 as uuid } from 'uuid';

interface Props {
    tehtavat: Tehtava[];
    setTehtavat: (arg0: Tehtava[]) => void;
}

const Koulutehtavat: React.FC<Props> = ({ tehtavat, setTehtavat }): React.ReactElement => {
    const [naytaPalautetut, setNaytaPalautetut] = useState<boolean>(false);


const handleStatusChange = (event: any, id: string) => {
    const selectedStatus = event.target.value as string;
    const updatedTehtavat = tehtavat.map((tehtava) => {
        if (tehtava.id === id) {
            return { ...tehtava, status: selectedStatus };
        }
        return tehtava;
    });
    setTehtavat(updatedTehtavat);
};

const handlePoisto = (id: string) => {
    if (window.confirm('Haluatko varmasti poistaa tämän tehtävän?')) {
        const updatedTehtavat = tehtavat.filter((tehtava) => tehtava.id !== id);
        setTehtavat(updatedTehtavat);
    }
};

    return (
        <>
        <Container sx={{ margin: 'auto'}}>
            <Typography variant="h3" sx={{ marginBottom: '20px', marginTop: '20px' }}>
                Koulutehtävät
            </Typography>
            <Table sx={{border:"1px solid #ccc", }}>
                <TableHead>
                    <TableRow sx={{backgroundColor: "#3460d1", }}>
                        <TableCell sx={{color: "white", fontWeight:"bold", fontSize: "16px"}}>Tehtävä</TableCell>
                        <TableCell sx={{color: "white", fontWeight:"bold", fontSize: "16px"}}>Deadline</TableCell>
                        <TableCell sx={{color: "white", fontWeight:"bold", fontSize: "16px"}}>Status</TableCell>
                        <TableCell sx={{color: "white", fontWeight:"bold", fontSize: "16px"}}>Opintojakso</TableCell>
                        <TableCell sx={{color: "white", fontWeight:"bold", fontSize: "16px"}}>Muistiinpanot</TableCell>
                        <TableCell sx={{color: "white", fontWeight:"bold", fontSize: "16px"}}>Poista</TableCell>
                    </TableRow>
                </TableHead>

                
                <TableBody>
                {tehtavat.map((tehtava: Tehtava, index: number) => {
                    if (!naytaPalautetut && tehtava.status === 'Palautettu') {
                        return null;
                    }
                    return (
                        <TableRow key={uuid()}>
                            <TableCell sx={{fontWeight:"bold"}}>{tehtava.nimi}</TableCell>
                            <TableCell>
                                <Typography
                                    style={{ color: differenceInDays(tehtava.deadline, new Date()) <= 7 ? 'red' : 'inherit' }}
                                >
                                    {format(tehtava.deadline, 'd.M.Y')}
                                </Typography>
                            </TableCell>
                            <TableCell>
                                <Select
                                    name="status"
                                    value={tehtava.status}
                                    onChange={(event) => handleStatusChange(event, tehtava.id)}
                                    sx={{
                                        borderRadius: '50px',
                                        width: '130px',
                                        textAlign: 'center',
                                        boxShadow: "#ccc 0px 8px 10px 0px",
                                        backgroundColor:
                                            tehtava.status === 'Tekemättä'
                                                ? '#FFDFE2'
                                                : tehtava.status === 'Työn alla'
                                                ? '#FDFFDF'
                                                : tehtava.status === 'Valmis'
                                                ? '#E1FFDF'
                                                : tehtava.status === 'Palautettu'
                                                ? '#DFFFFF'
                                                : 'transparent',
                                        border:
                                            tehtava.status === 'Tekemättä'
                                                ? 'solid 3px #FF91A0'
                                                : tehtava.status === 'Työn alla'
                                                ? 'solid 3px #FFF791'
                                                : tehtava.status === 'Valmis'
                                                ? 'solid 3px #ADFF91'
                                                : tehtava.status === 'Palautettu'
                                                ? 'solid 3px #A6EFF3'
                                                : 'transparent'
                                    }}
                                    MenuProps={{ sx: { backgroundColor: 'transparent' } }}
                                >
                                    <MenuItem value="Tekemättä">Tekemättä</MenuItem>
                                    <MenuItem value="Työn alla">Työn alla</MenuItem>
                                    <MenuItem value="Valmis">Valmis</MenuItem>
                                    <MenuItem value="Palautettu">Palautettu</MenuItem>
                                </Select>
                            </TableCell>
                            <TableCell>{tehtava.opintojakso}</TableCell>
                            <TableCell>{tehtava.muistiinpanot}</TableCell>
                            <TableCell>
                                <IconButton onClick={() => handlePoisto(tehtava.id)} edge="end">
                                    <DeleteIcon />
                                </IconButton>
                            </TableCell>
                        </TableRow>
                    );
                })}

                </TableBody>
            </Table>
            <FormControlLabel
                control={<Switch checked={naytaPalautetut} onChange={() => setNaytaPalautetut(!naytaPalautetut)} />}
                label="Näytä palautetut tehtävät"
                sx={{ marginTop: '20px', marginLeft: "20px", marginBottom: "20px" }}
            />
                <Button
                    variant="text"
                    component={Link}
                    to="/uusi"
                    sx={{
                        float: 'right',
                        marginTop: '20px',
                        marginRight: '20px'
                    }}
                >
                    Lisää uusi tehtävä
                </Button>
        </Container>

        </>
    );
};


export default Koulutehtavat;