import React, { useEffect, useRef, useState } from 'react';
import UusiTehtava from './components/UusiTehtava';
import Koulutehtavat from './components/Koulutehtavat';
import { Route, Routes } from 'react-router-dom';


import { v4 as uuid } from 'uuid';

const App : React.FC = () : React.ReactElement => {

  const kaynnistetty : React.MutableRefObject<boolean> = useRef(false);

  const [tehtavat, setTehtavat] = useState<Tehtava[]>([
                                          {
                                            id : uuid(),
                                            nimi : "Essee", 
                                            deadline : new Date(2024, 5, 14),
                                            status : "Tekemättä",
                                            opintojakso : "Ohjelmointi",
                                            muistiinpanot : "Kirjan kappaleet 2-5"
                                          },
                                          {
                                            id : uuid(),
                                            nimi : "Tentti", 
                                            deadline : new Date(2024, 7, 11),
                                            status : "Tekemättä",
                                            opintojakso : "Viestintä"
                                          },
                                          {
                                            id : uuid(),
                                            nimi : "Ryhmätyö", 
                                            deadline : new Date(2024, 4, 11),
                                            status : "Tekemättä",
                                            opintojakso : "Graafinen suunnittelu"
                                          },
                                          {
                                            id : uuid(),
                                            nimi : "Raportti", 
                                            deadline : new Date(),
                                            status : "Tekemättä",
                                            opintojakso : "Ohjelmointi"
                                          },
                                          {
                                            id : uuid(),
                                            nimi : "Tentti", 
                                            deadline : new Date(2024, 8, 3),
                                            status : "Tekemättä",
                                            opintojakso : "Graafinen suunnittelu",
                                            muistiinpanot : "Lainaa tenttikirja"
                                          },
                                          {
                                            id : uuid(),
                                            nimi : "OT 3", 
                                            deadline : new Date(2024, 6, 30),
                                            status : "Palautettu",
                                            opintojakso : "Ohjelmointi",
                                          }
                                        ]);

  useEffect(() => {

    if (!kaynnistetty.current) {
    
    if (localStorage.getItem("koulutehtavat")) {

      setTehtavat(JSON.parse(String(localStorage.getItem("koulutehtavat"))).map((tehtava : Tehtava) => {

        return {
          ...tehtava,
          deadline : new Date(tehtava.deadline)
        }
    }))
  }
}

  return () => {
    kaynnistetty.current = true;
  }
  }, []); 

  useEffect(() => {
    
    localStorage.setItem("koulutehtavat", JSON.stringify(tehtavat));
  }, [tehtavat]); 
  
  

  return (
    <>
      
        <Routes>

          <Route path="/" element={<Koulutehtavat tehtavat={tehtavat} setTehtavat={setTehtavat} />} />

          <Route path="/uusi" element={<UusiTehtava tehtavat={tehtavat} setTehtavat={setTehtavat} defaultStatus='Tekemättä' />}  />

        </Routes>
     


    </>
  );
}

export default App;
