import React, { useRef, useState } from 'react';
import { Button, Container, TextField, Typography } from '@mui/material';
import { Link, NavigateFunction, useNavigate } from 'react-router-dom';
import { DatePicker, LocalizationProvider } from '@mui/x-date-pickers';
import { AdapterDateFns } from '@mui/x-date-pickers/AdapterDateFns';
import CheckRoundedIcon from '@mui/icons-material/CheckRounded';
import ClearRoundedIcon from '@mui/icons-material/ClearRounded';
import { v4 as uuid } from 'uuid';

interface Props {
    tehtavat: Tehtava[];
    setTehtavat: (arg0: Tehtava[]) => void;
    defaultStatus: string;
}

const UusiTehtava: React.FC<Props> = ({ tehtavat, setTehtavat, defaultStatus }): React.ReactElement => {
    const [pvmAika, setPvmAika] = useState<Date>(new Date());
    const opintojaksoRef: React.MutableRefObject<HTMLInputElement | undefined> = useRef<HTMLInputElement>();
    const muistiinpanotRef: React.MutableRefObject<HTMLInputElement | undefined> = useRef<HTMLInputElement>();
    const navigate: NavigateFunction = useNavigate();
    const uusiTehtavaRef: React.MutableRefObject<HTMLInputElement | undefined> = useRef<HTMLInputElement>();



    const lisaaTehtava = (): void => {
        if (!uusiTehtavaRef.current!.value){
            alert('Täytä pakollinen kenttä: Tehtävän nimi');
            return;
        }

        let apuTehtava: Tehtava = {
            id : uuid(),
            nimi: uusiTehtavaRef.current!.value,
            deadline: pvmAika,
            status: defaultStatus,
            opintojakso: opintojaksoRef.current!.value,
            muistiinpanot: muistiinpanotRef.current!.value
        };

        setTehtavat([...tehtavat, apuTehtava]);
        navigate("/");
    };

    return (
        <LocalizationProvider dateAdapter={AdapterDateFns}>
            <Container sx={{margin: "auto", marginTop: "40px", maxWidth: "600px", padding: "20px", boxShadow: "#ccc 0px 7px 29px 0px"}}>
            <Typography variant= "h3"
                sx={{marginBottom: "20px", marginTop: "20px"}}>
                    Uusi tehtävä
            </Typography>
            <TextField
                label="Tehtävän nimi"
                inputRef={uusiTehtavaRef}
                required
                variant="outlined"
                fullWidth={true}
                sx={{ marginBottom: "10px" }}
            />
            <TextField
                label="Opintojakso"
                inputRef={opintojaksoRef}
                variant="outlined"
                fullWidth={true}
                sx={{ marginBottom: "10px" }}
            />
            <DatePicker
                label="Deadline"
                value={pvmAika}
                onChange={(uusiPvm: Date | null) => setPvmAika(uusiPvm!)}
            />
            
            <TextField 
                placeholder='Muistiinpanot'
                inputRef={muistiinpanotRef}
                fullWidth= {true}
                sx={{
                    marginTop: "10px",
                    marginBottom: "10px"
                }}
            />
            <Button
                variant="contained"
                onClick={lisaaTehtava}
                sx={{
                    marginTop: "10px",
                    marginRight: "10px"
                    }}
            ><CheckRoundedIcon /> Lisää</Button>
            <Button
                sx={{marginTop: "10px"}}
                component={Link}
                to="/"
            > <ClearRoundedIcon/> Peruuta</Button>

            </Container>
        </LocalizationProvider>
    );
};

export default UusiTehtava;

